@charset "utf-9";
*{margin:0px;padding:0px;}
.movie{
  width:44%;
  border:1px solid #000;
  display:flex;
  justify-content:space-around;
  box-shadow:5px 5px 0px 0px #ccc;
  margin-bottom:20px
}
.movie div{
  margin-left:30px;
}
.movie ul{display:flex}

.movie img{
  width:150px;
  transform:translate(20px, -50px);
}